export default function (injection) {
    Object.assign(injection, {
        board: {
            lists: [
                {
                    content: '<p><strong></strong>' +
                        '<a href="https://github.com/twilroad" target="_blank"></a>' +
                        '<a href="https://www.zuohuadong.cn" target="_blank"></a>' +
                        '<a href="https://github.com/medz" target="_blank">Seven Du</a>' +
                        '<a href="https://github.com/QiyueShiyi" target="_blank">Luff</a>' +
                        '<a href="https://weibo.com/u/2181906365" target="_blank"></a>' +
                        '<a href="https://weibo.com/u/3189357545" target="_blank"></a>' +
                        '<a href="http://weibo.com/u/3854583077" target="_blank"></a>' +
                        '<a href="https://weibo.cn/u/3258236872/" target="_blank"></a>' +
                        '<a href="http://weibo.com/u/5592753739" target="_blank"></a>' +
                    '</p>' +
                    '<p><strong></strong>' +
                        '<a href="https://github.com/LitoMore" target="_blank">LitoMore</a>' +
                        '<a href="https://github.com/ganlanshu0211" target="_blank"></a>' +
                        '<a href="https://github.com/ToxinSting" target="_blank">Rayle</a>' +
                        '<a href="https://github.com/cloudsher" target="_blank">cloudSher</a>' +
                        '<a href="http://www.lovetd.cn" target="_blank"></a>' +
                    '</p>',
                    span: 12,
                    title: '',
                    type: 'html',
                },
                {
                    content: ' Button ',
                    link: 'http://www.hao123.com',
                    span: 12,
                    theme: 'primary',
                    title: ' Button ',
                    type: 'button',
                },
                {
                    content: ' Text ',
                    span: 12,
                    title: ' Text ',
                    type: 'text',
                },
                {
                    content: ' Html ',
                    span: 12,
                    title: ' Html ',
                    type: 'html',
                },
                {
                    content: {
                        title: {
                            text: 'Notadd ',
                        },
                        tooltip: {},
                        xAxis: {
                            data: ['Shirt', 'Sweater', 'Chiffon Shirt', 'Pants', 'High Heels', 'Socks'],
                        },
                        yAxis: {},
                        series: [
                            {
                                name: 'Sales',
                                type: 'bar',
                                data: [5, 20, 36, 10, 10, 20],
                            },
                        ],
                    },
                    span: 12,
                    style: 'height: 300px;',
                    title: ' Chart ',
                    type: 'chart',
                },
            ],
            inject(data) {
                injection.board.lists.push(data);
            },
        },
    });
}



